  mounted() {
    // this.callTime();
    const _this = this
    this.createIns().then(res => {
        const contract_whitelist = new res.eth.Contract(_this.con_abi.contract_abi, _this.con_abi.contract_addr);
        const contract_usdt = new res.eth.Contract(_this.con_usdt.usdt_abi, _this.con_usdt.usdt_addr);
        res.eth.getAccounts(function(err, result) {
            let address = result[0];
            this.address = address            
            

            contract_whitelist.methods.addressExists(address).call().then(function (result2) {
                console.log('result:', result2)
                if (result2) { // 购买过
                    // TODO 按钮文案变为 "已购买" 且不可点击
                    _this.isAuth = true;
                    _this.isBuy = true;
                } else {
                    contract_usdt.methods.allowance(address, _this.con_abi.contract_addr).call().then(function (amount) {
                        let BN = res.utils.BN;
                        let rs = new BN(amount).sub(new BN(_this.price));
                        if (rs.negative === 0) { // 已授权 可购买
                            // TODO 按钮文案变为 购买，可点击
                            _this.isAuth = true;
                            _this.isAuth = true;
                        } else { // 未授权
                            // TODO 按钮文案变为 授权，可点击
                            _this.isAuth = true;
                        }
                    });
                }
            });
        });
        
    })
  },
  methods: {
    auth() {
        const _this = this;
        this.createIns().then(res => {
            const contract_usdt = new res.eth.Contract(_this.con_usdt.usdt_abi, _this.con_usdt.usdt_addr);
            contract_usdt.methods.approve(_this.con_abi.contract_addr, _this.price).send({from: _this.address})
                .on('receipt', function (receipt) {
                    // TODO 按钮文案变为 购买，可点击
                    _this.isBuy = true;
                })
                .on('error', function (err) {
                    // TODO 错误提示
                    alert(err.message)
                });
        })
    },
    createIns() {
        return new Promise((resolve, reject) => {
            if (window.ethereum) {
                const web3 = new this.$Web3(window.ethereum);
                try {
                    // Request account access if needed
                    window.ethereum.enable();
                    // Acccounts now exposed
                    web3.eth.getChainId().then(function (result) {
                        if (result === 56)
                            resolve(web3);
                        else if (window.ethereum)
                            window.ethereum.request({
                                method: 'wallet_switchEthereumChain',
                                params: [{chainId: '0x38'}],
                            }).then(() => {
                                resolve(web3);
                            }).catch((err) => {
                                if (err.code === 4092) {
                                    window.ethereum.request({
                                        method: 'wallet_addEthereumChain',
                                        params: [{
                                            chainId: '0x38',
                                            chainName: 'Binance Smart Chain Mainnet',
                                            nativeCurrency: {
                                                name: 'Binance Coin',
                                                symbol: 'BNB',
                                                decimals: 18
                                            },
                                            rpcUrls: ['https://bsc-dataseed1.defibit.io'],
                                            blockExplorerUrls: ['https://bscscan.com']
                                        }]
                                    }).then((result) => {
                                        resolve(web3);
                                        return result
                                    }).catch(() => {
                                        reject('请切换到币安智能链主网');
                                    });
                                } else
                                    reject('请切换到币安智能链主网');
                            });
                        else
                            reject('请切换到币安智能链主网');
                    });
                } catch (error) {
                    reject(error);
                }
            }
            // Legacy dapp browsers...
            else if (window.web3) {
                // Use Mist/MetaMask's provider.
                const web3 = window.web3;
                console.log("Injected web3 detected.");
                resolve(web3);
            }
            // Fallback to localhost; use dev console port by default...
            else {
                reject('No available wallet');
            }
        })
        
    },
    subscribe() {
        const _this = this;
        this.createIns().then(res => {
            const contract_whitelist = new res.eth.Contract(_this.con_abi.contract_abi, _this.con_abi.contract_addr);
            contract_whitelist.methods.buy().send({from: _this.address, gas: 40000})
                .on('receipt', function (receipt) {
                    // TODO 购买成功，按钮文案变为 "已购买" 且不可点击
                    _this.btnStatus.isAuth = true;
                    _this.btnStatus.isBuy = true;
                })
                .on('error', function (err) {
                    // TODO 错误提示
                    console.log(err);
                });
        })
    },
    open() {
        alert('暂未开放')
    },
    callTime () {
      clearInterval(this.timer);
      const _this = this;
      this.timer = setInterval(() => {
          _this.start_time -= 1;
          _this.countDown();
      }, 1000);
    },
    timeFormat(param) {
      return param < 10 ? '0' + param : param;
    },
    countDown () {
      let d = parseInt(this.start_time / (24 * 60 * 60));
      let h = parseInt(this.start_time / (60 * 60) % 24);
      let m = parseInt(this.start_time / 60 % 60);
      let s = parseInt(this.start_time % 60);
      
      this.time = {
        day: this.timeFormat(d),
        hour: this.timeFormat(h),
        min: this.timeFormat(m),
        sec: this.timeFormat(s)
      }
    }
  }
}
